/* eslint-disable*/
function initIntercom() {
	const params = new URLSearchParams(window.location.search);
	const isPpaySettings = params.get('page') && (params.get('page')?.slice(0, 8) === 'peachpay');
	const isPeachPayGatewaySettings = params.get('section') && (params.get('section')?.slice(0, 8) === 'peachpay');
	if (isPpaySettings || isPeachPayGatewaySettings) {
		initIntercomSupport();
	}
}

/**
 * Initializes event listeners in the PeachPay settings navigation.
 */
function initIntercomSupport() {
	window.intercomSettings = {
		api_base: 'https://api-iam.intercom.io',
		app_id: 'moaljum2',
	};

	// @ts-expect-error
    (function () { var w = window; var ic = w.Intercom; if (typeof ic === "function") { ic('reattach_activator'); ic('update', w.intercomSettings); } else { var d = document; var i = function () { i.c(arguments); }; i.q = []; i.c = function (args) { i.q.push(args); }; w.Intercom = i; var l = function () { var s = d.createElement('script'); s.type = 'text/javascript'; s.async = true; s.src = 'https://widget.intercom.io/widget/moaljum2'; var x = d.getElementsByTagName('script')[0]; x.parentNode.insertBefore(s, x); }; if (document.readyState === 'complete') { l(); } else if (w.attachEvent) { w.attachEvent('onload', l); } else { w.addEventListener('load', l, false); } } })();

	// Add event listener to Support button
	document.querySelectorAll('#peachpay-nav .support-icon-link, .peachpay-side-nav .support-icon-link').forEach($link => {
		$link.addEventListener('click', event => {
			event.preventDefault();
			window.Intercom('show');
		});
	});

	const params = new URLSearchParams(window.location.search);
	const openIntercom = params.get('intercom') === 'true';

	if (openIntercom) {
		const supportLink = document.querySelector('.support-icon-link') as HTMLElement | null;
		if (supportLink) {
			supportLink.click();
	  	}
	}
}

export {initIntercom};
