import {$qsAll} from '../../@shared/ts/dom';
import {contentTooWide} from './ui-utils';

/**
 * Initializes homepage UI states and event listeners.
 */
function initHomepageEvents() {
	$qsAll('.dashboard-container .sub-logos-toggle').forEach($button => {
		$button.addEventListener('click', (event: Event) => {
			event.stopImmediatePropagation();
			const $subLogosContainer = $button.closest('.sub-logos');
			const $subLogosToggle = $subLogosContainer?.querySelector('.sub-logos-toggle');
			if ($subLogosContainer?.hasAttribute('expanded')) {
				$subLogosContainer?.removeAttribute('expanded');
				if ($subLogosToggle) {
					$subLogosToggle.innerHTML = 'See more';
				}
			} else {
				$subLogosContainer?.setAttribute('expanded', '');
				if ($subLogosToggle) {
					$subLogosToggle.innerHTML = 'See less';
				}
			}
		});
	});

	fitAnalyticsCardContent();

	unHideElements();

	window.addEventListener('resize', () => {
		fitAnalyticsCardContent();
	});
}

function fitAnalyticsCardContent() {
	$qsAll('.dashboard-section-analytics-array-data-container').forEach($arrayDataContainer => {
		if (window.innerWidth > 620 && $arrayDataContainer.classList.contains('column-view')) {
			$arrayDataContainer.classList.remove('column-view');
		}

		if (!$arrayDataContainer.classList.contains('column-view') && contentTooWide($arrayDataContainer)) {
			$arrayDataContainer.classList.add('column-view');
		}
	});
}

function unHideElements() {
	$qsAll('.opacity-zero').forEach(($element: HTMLElement) => {
		$element.classList.remove('opacity-zero');
	});
}

export {initHomepageEvents};
